import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";



const Breadcrumb = (props) => {


    useEffect(() => {
        // console.log("props", props);
    });

    return (
        <div className="breadcrumb_section tittle_breaddrumb p-4 bg_gray page-title-mini">
            <div className="container">
                <div className="row align-items-center">
                    <div className="col-md-12">
                        <ol className="breadcrumb justify-content-md-start">
                            <li className="breadcrumb-item">
                                <Link to="/">
                                    <i className="fa fa-home" />
                                </Link>
                            </li>
                            <li className="breadcrumb-item">
                                <Link to="/shop">Electronics</Link>
                            </li>
                            <li className="breadcrumb-item active">Mobile</li>
                        </ol>
                    </div>
                </div>
            </div>
        </div>
    );
};
export default Breadcrumb;
