import React, { useEffect, useState } from "react";
import { Link, withRouter } from "react-router-dom";
import ReactHtmlParser from 'react-html-parser';
import { connect } from "react-redux";
import { loadPages } from "../../store/actions/PageActions";
import Breadcrumb from "../../breadcrumb/Breadcrumb";

const SinglePage = (props) => {
    const { init, pages, match } = props;
    const result = pages.result ? pages.result : {};
    const [slug, setSlug] = useState("");
    const [page, setPage] = useState({});
    const [loading, setLoading] = useState(true);
    const [loadInit, setLoadInit] = useState(false);
    const [newLoading, setNewLoading] = useState(true);

    const load_new_pages = (slug) => {
        props.loadPages(result, slug);
    };

    const load_select_pages = (pages, result, slug) => {
        if (pages.status) {
            if (Object.keys(result).length) {
                let tryLoad = true;
                for (let key1 in result) {
                    if (key1 === slug) {
                        setPage(result[key1].items[0]);
                        tryLoad = false;
                    }
                }
                if (tryLoad && !newLoading) {
                    setNewLoading(true);
                    console.log("inside result");
                    load_new_pages(slug);
                }
            }
        } else {
            if (!loadInit && slug) {
                console.log("load init");
                setLoadInit(true);
                load_new_pages(slug);
            }
        }
    };

    useEffect(() => {
        let newSlug = match.params.slug;
        if (slug !== newSlug) {
            setSlug(newSlug);
            setPage({});
            setLoading(true);
            if (newLoading && slug) {
                setNewLoading(false);
            }
        }
        if (loading) {
            load_select_pages(pages, result, slug);
        }
    });

    return (
        <>
            <Breadcrumb />
            <article className="section pt-4">
                <div className="container">
                    <div className="row justify-content-center">
                        <div className="col-md-12">
                            <div className="heading_s1">
                                <h3>{page.content_heading}</h3>
                            </div>
                            <div
                                className="accordion accordion_style1"
                            >                              
                              {ReactHtmlParser(page.content)}
                            </div>
                        </div>
                   </div>
                </div>
            </article>
        </>
    );
};

const mapStateToProps = (state) => ({
    init: state.init,
    pages: state.pages,
});

export default connect(mapStateToProps, { loadPages })(withRouter(SinglePage));
