import React from 'react';
import {Link} from "react-router-dom";
import {Accordion, Button} from "react-bootstrap";

const SidebarCategoryList = (props) => {
   const {children_one} = props;

   const accordion_right_btn = (children_id) => (
      <div className="categories_num">
         <Accordion.Toggle
            as={Button}
            variant="button"
            className="p-0"
            eventKey={children_id}
         >
            <i className="fas fa-angle-right"/>
         </Accordion.Toggle>
      </div>
   );


   return (
      <li>
         <div className="clearfix">
            <Link to={`/category/${children_one.id}`}>
               {children_one.name}
            </Link>
            {
               children_one.children_data.length > 0 &&
               accordion_right_btn(children_one.id)
            }
         </div>
         {
            children_one.children_data.length > 0 &&
            (
               <Accordion.Collapse
                  eventKey={children_one.id}
               >
                  <ul className="ml-2 widget_categories">
                     {
                        children_one.children_data.map((children_two, key) =>
                           <li key={children_two.id} key={children_two.id}>
                              <div className="clearfix">
                                 <Link to={`/category/${children_two.id}`}>
                                    {children_two.name}
                                 </Link>
                                 {
                                    children_two.children_data.length > 0 &&
                                    accordion_right_btn(children_two.id)
                                 }
                              </div>
                              {
                                 children_two.children_data.length > 0 &&
                                 (
                                    <Accordion.Collapse
                                       eventKey={children_two.id}
                                    >
                                       <ul className="ml-2 widget_categories">
                                          <li>List Item</li>
                                          <li>List Item</li>
                                          <li>List Item</li>
                                       </ul>
                                    </Accordion.Collapse>
                                 )
                              }
                           </li>
                        )
                     }
                  </ul>
               </Accordion.Collapse>
            )
         }

      </li>
   );
};

export default SidebarCategoryList;