import React from "react";
import { Link } from "react-router-dom";
import { connect } from "react-redux";
import { login } from "../store/actions/authActions";
import { Tabs, Tab } from "react-bootstrap";
import "react-phone-number-input/style.css";
import PhoneInput, {
    isValidPhoneNumber,
    parsePhoneNumber,
} from "react-phone-number-input";
import Input from "react-phone-number-input/input";

class Login extends React.Component {
    state = {
        isValidPhone: false,
        email: "",
        phone: "",
        error: {},
    };

    static getDerivedStateFromProps(nextProps, prevState) {
        if (
            JSON.stringify(nextProps.auth.error) !==
            JSON.stringify(prevState.error)
        ) {
            return {
                error: nextProps.auth.error,
            };
        }
        return null;
    }

    submitPhoneHandler = (event) => {
        event.preventDefault();
        let { phone } = this.state;
        if (!phone) {
            alert("Mobile must not empty");
        } else {
            this.props.login(
                {
                    phone: phone,
                },
                this.props.history
            );
        }
    };

    submitEmailHandler = (event) => {
        event.preventDefault();
        let { email } = this.state;
        if (!email) {
            alert("Email must not empty");
        } else {
            this.props.login(
                {
                    email: email,
                },
                this.props.history
            );
        }
    };

    handleValidPhone = () => {
        const { phone } = this.state;
        let checkValid = false;
        if (isValidPhoneNumber(phone)) {
            checkValid = true;
        }
        this.setState({ isValidPhone: checkValid });
    };

    changeHandler = (event) => {
        this.setState({
            [event.target.name]: event.target.value,
        });
    };

    render() {
        let { isValidPhone, email, phone, error } = this.state;
        return (
            <div className="bg-img1 py-5">
                <div className="container">
                    <div className="row mt-3 justify-content-center">
                        <div className="col-md-4 mb-3">
                            <div className="loginright shadow">
                                <Tabs
                                    className="nav-justified"
                                    defaultActiveKey="mobile"
                                    transition={false}
                                    id="login-tab-example"
                                >
                                    <Tab eventKey="mobile" title="Mobile">
                                        <form
                                            onSubmit={this.submitPhoneHandler}
                                        >
                                            <div className="form-group">
                                                <label htmlFor="phone">
                                                    Mobile
                                                </label>
                                                <Input
                                                    name="phone"
                                                    country="BD"
                                                    id="phone"
                                                    international
                                                    withCountryCallingCode
                                                    className="form-control"
                                                    style={{
                                                        borderWidth: 0,
                                                        borderBottomWidth:
                                                            "1px",
                                                        borderRadius: 0,
                                                    }}
                                                    value={phone || ""}
                                                    onChange={
                                                        (phone) => this.setState({phone})
                                                    }
                                                    error={
                                                        phone
                                                            ? isValidPhoneNumber(
                                                                  phone
                                                              )
                                                                ? undefined
                                                                : "Invalid phone number"
                                                            : "Phone number required"
                                                    }
                                                />
                                                {phone &&
                                                isValidPhoneNumber(phone) ? (
                                                    <small className="form-text text-success">{` Phone number accepted`}</small>
                                                ) : (
                                                    <small className="form-text text-warning">{`Enter Mobile (e.g. 01734905649)`}</small>
                                                )}
                                            </div>

                                            <button
                                                type="submit"
                                                className="btn btn-primary btn-block"
                                            >
                                                Next
                                            </button>
                                        </form>
                                    </Tab>
                                    <Tab eventKey="email" title="Email">
                                        <form
                                            onSubmit={this.submitEmailHandler}
                                        >
                                            <div className="form-group">
                                                <label htmlFor="email">
                                                    Email
                                                </label>
                                                <input
                                                    type="email"
                                                    className="form-control"
                                                    name="email"
                                                    id="email"
                                                    placeholder="Enter Email (e.g. example@mail.com)"
                                                    aria-label="email"
                                                    value={email || ""}
                                                    onChange={
                                                        this.changeHandler
                                                    }
                                                />
                                            </div>
                                            <button
                                                type="submit"
                                                className="btn btn-primary btn-block"
                                            >
                                                Next
                                            </button>
                                        </form>
                                    </Tab>
                                </Tabs>
                                <div className="different_login">
                                    <span> or login using</span>
                                </div>

                                <ul className="btn-login list_none text-center">
                                    <li className="d-block mb-2">
                                        <a
                                            href="#"
                                            className="btn btn-block btn-facebook"
                                        >
                                            <i className="ion-social-facebook"></i>
                                            Login with Facebook
                                        </a>
                                    </li>
                                    <li className="d-block mb-2">
                                        <a
                                            href="#"
                                            className="btn btn-block btn-google"
                                        >
                                            <i className="fab fa-google"></i>
                                            Login with Google
                                        </a>
                                    </li>
                                </ul>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

const mapStateToProps = (state) => ({
    auth: state.auth,
});

export default connect(mapStateToProps, { login })(Login);
