import axios from "axios";

import * as Types from "./types";

const generate_plain_category_collections = (dataMenu) => {
    let menus = dataMenu.hasOwnProperty("children_data")
        ? dataMenu.children_data
        : [];
    let categoryCollection = [];
    menus.map((menu, key) => {
        categoryCollection.push(menu);
        let menusTwo = menu.children_data;
        if (menusTwo.length) {
            menusTwo.map((menuTwo) => {
                categoryCollection.push(menuTwo);
                let menusThree = menuTwo.children_data;
                if (menusThree.length) {
                    menusThree.map((menusFour) => {
                        categoryCollection.push(menusFour);
                    });
                }
            });
        }
    });

    return categoryCollection;
};

export const loadInit = () => (dispatch) => {
    axios
        .post("/v1/init")
        .then((response) => {
            let data = response.data;
            if (data.status) {
                let dataMenu = data.hasOwnProperty("menus") ? data.menus : {};
                let categories = generate_plain_category_collections(dataMenu);
                dispatch({
                    type: Types.LOAD_INIT,
                    payload: {
                        init: {
                            status: true,
                            menus: dataMenu,
                            categories: categories ,
                        },
                    },
                });
            }
        })
        .catch((error) => {
            console.log(error);
        });
};

