import axios from "axios";
import * as Types from "./types";

const with_all_children_ids = (category = {}) => {
    let children = category.hasOwnProperty("children_data")
        ? category.children_data
        : [];
    let collections = [category.id];
    children.map((childOne) => {
        collections.push(childOne.id);
        let childTwos = childOne.hasOwnProperty("children_data")
            ? childOne.children_data
            : [];
        childTwos.map((childTwo) => {
            collections.push(childTwo.id);
        });
    });

    return collections;
};

export const loadProducts =
    (oldProducts = {}, selectCategory = {}) =>
    (dispatch) => {
        let withChildren = with_all_children_ids(selectCategory);
        const oldResult = oldProducts.result ? oldProducts.result : [];
        axios
            .post("/v1/products", { cat_id: JSON.stringify(withChildren) })
            .then((response) => {
                const filterKey = "q" + selectCategory.id;
                const responseData = response.data;
                const result = responseData.result;
                const newResult = { [filterKey]: result };
                dispatch({
                    type: Types.LOAD_PRODUCTS,
                    payload: {
                        products: {
                            status: true,
                            result: { ...oldResult, ...newResult },
                        },
                    },
                });
            })
            .catch((error) => {
                console.log(error);
            });
    };

