import React from "react";
import { Link } from "react-router-dom";

const CategoryItem = (props) => {
    const { parentCategory } = props;
    const children_data = parentCategory.hasOwnProperty("children_data")
        ? parentCategory.children_data
        : [];

    return (
        <li className="dropdown dropdown-mega-menu">
            {children_data.length ? (
                <>
                    <Link
                        className="dropdown-item nav-link dropdown-toggler"
                        to={"/category/" + parentCategory.id}
                        data-toggle="dropdown"
                    >
                        <i className="flaticon-tv"></i>{" "}
                        <span>{parentCategory.name}</span>
                    </Link>
                    <div className="dropdown-menu">
                        <ul className="mega-menu d-lg-flex">
                            {children_data.map((children_one, key) => (
                                <React.Fragment key={children_one.id}>
                                    {children_one.children_data.length ? (
                                        <li className="mega-menu-col col-lg-4">
                                            <ul>
                                                <li className="dropdown-header">
                                                    {children_one.name}
                                                </li>
                                                {children_one.children_data.map(
                                                    (children_two, key) => (
                                                        <li
                                                            key="key"
                                                            key={
                                                                children_two.id
                                                            }
                                                        >
                                                            <Link
                                                                className="dropdown-item nav-link nav_item"
                                                                to={
                                                                    `/category/` +
                                                                    children_two.id
                                                                }
                                                            >
                                                                {
                                                                    children_two.name
                                                                }
                                                            </Link>
                                                        </li>
                                                    )
                                                )}
                                            </ul>
                                        </li>
                                    ) : (
                                        <li>
                                            <Link
                                                className="dropdown-item nav-link nav_item"
                                                to={
                                                    `/category/` +
                                                    children_one.id
                                                }
                                            >
                                                {children_one.name}
                                            </Link>
                                        </li>
                                    )}
                                </React.Fragment>
                            ))}
                        </ul>
                    </div>
                </>
            ) : (
                <Link
                    className="dropdown-item nav-link nav_item"
                    to={"/" + parentCategory.name}
                >
                    <i className="flaticon-watch"></i>{" "}
                    <span>{parentCategory.name}</span>
                </Link>
            )}
        </li>
    );
};

