import React from "react";
import { Link } from "react-router-dom";
import { connect } from "react-redux";
import { register } from "../store/actions/authActions";

class Register extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            name: "",
            email: "",
            phone: "",
            otp: "",
            auth: {},
            error: {},
        };
    }

    static getDerivedStateFromProps(nextProps, prevState) {
        if (
            JSON.stringify(nextProps.auth.error) !==
            JSON.stringify(prevState.error)
        ) {
            return {
                error: nextProps.auth.error,
            };
        }
        return null;
    }

    componentDidMount() {
        const {
            auth: { isAuthenticated, user },
            history,
        } = this.props;
        if (isAuthenticated === false) {
            this.setState({
                phone: user.phone,
                email: user.email,
                name: user.name,
            });
        } else if (user.isAuthenticated) {
            history.push("/dashboard");
        }
        if (Object.keys(user).length === 0) {
            history.push("/login");
            console.log("logout");
        }
    }

    submitHandler = (event) => {
        event.preventDefault();
        let { name, email, phone } = this.state;
        if (!name) {
            alert("Name must not empty");
        } else if (!email) {
            alert("Email must not empty");
        } else if (!phone) {
            alert("Phone must not empty");
        } else {
            this.props.register({ firstname: name, lastname: name, email, phone }, this.props.history);
        }
    };

    changeHandler = (event) => {
        this.setState({
            [event.target.name]: event.target.value,
        });
    };

    render() {
        let { name, email, phone, error } = this.state;
        return (
            <div className="bg-img1 py-5">
                <div className="container">
                    <div className="row mt-3 justify-content-center">
                        <div className="col-md-4 mb-3">
                            <div className="loginright shadow">
                                <h3 className="pb-3">Create an account</h3>
                                <form onSubmit={this.submitHandler}>
                                    <div className="form-group">
                                        <label htmlFor="exampleInputEmail1">
                                            Mobile
                                        </label>
                                        <input
                                            type="text"
                                            className="form-control"
                                            style={{
                                                borderWidth: 0,
                                                borderBottomWidth: "1px",
                                                borderRadius: 0,
                                            }}
                                            readOnly={true}
                                            onChange={this.changeHandler}
                                            value={phone || ""}
                                        />
                                        <small
                                            id="passwordHelpInline"
                                            className="text-muted"
                                        >
                                            We sent a code message to verify
                                            your mobile number
                                        </small>
                                    </div>
                                    <div className="form-group">
                                        <label htmlFor="name">Name</label>
                                        <input
                                            type="text"
                                            className="form-control"
                                            name="name"
                                            id="name"
                                            placeholder="Enter Full Name"
                                            value={name || ""}
                                            onChange={this.changeHandler}
                                        />
                                    </div>
                                    <div className="form-group">
                                        <label htmlFor="email">Email</label>
                                        <input
                                            type="text"
                                            name="email"
                                            className="form-control"
                                            id="email"
                                            placeholder="Enter Email (e.g. example@mail.com)"
                                            value={email || ""}
                                            onChange={this.changeHandler}
                                        />
                                    </div>

                                    <button
                                        type="submit"
                                        className="btn btn-primary btn-block"
                                        title="Register"
                                    >
                                        Register
                                    </button>
                                </form>
                                <hr />
                                <div className="form-note pb-5 text-center">
                                    Already have an Account?{" "}
                                    <Link to="/login">Login Here</Link>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

const mapStateToProps = (state) => ({
    auth: state.auth,
});

export default connect(mapStateToProps, { register })(Register);
