import React, { useState, useEffect } from "react";
import { Link, withRouter } from "react-router-dom";
import { connect } from "react-redux";
import OwlCarousel from "react-owl-carousel";

const ShopByCats = (props) => {
    const { init } = props;
    const [children, setChildren] = useState([]);

    useEffect(() => {
        if (init.status) {
            const menus = init.hasOwnProperty("menus") ? init.menus : {};
            const children_data = menus.hasOwnProperty("children_data")
                ? menus.children_data
                : [];
            setChildren(children_data);
        }
    });

    return (
        <div className="section small_pb small_pt">
            <div className="container">
                <div className="row">
                    <h2 className="h2all">
                        <b>Shop</b> by Categories
                    </h2>
                </div>
                <div className="row align-items-center">
                    <div className="col-12">
                        {children.length ? (
                            <OwlCarousel
                                className="cat_slider cat_style1 mt-4 mt-md-0 carousel_slider owl-carousel owl-theme nav_style5"
                                loop={true}
                                margin={30}
                                nav={true}
                                navText={[
                                    '<i class="ion-ios-arrow-left"></i>',
                                    '<i class="ion-ios-arrow-right"></i>',
                                ]}
                                dots={false}
                                responsive={{
                                    0: { items: 1 },
                                    480: { items: 2 },
                                    576: { items: 4 },
                                    768: { items: 5 },
                                    991: { items: 6 },
                                    1199: { items: 8 },
                                }}
                            >
                                {children.map((category, key) => (
                                    <div className="item" key={key}>
                                        <div className="categories_box">
                                            <Link
                                                to={"/category/" + category.id}
                                            >
                                                <img
                                                    src="/assets/images/cat_img5.png"
                                                    alt={category.name}
                                                />
                                                <span>{category.name}</span>
                                            </Link>
                                        </div>
                                    </div>
                                ))}
                            </OwlCarousel>
                        ) : (
                            <p>loading shopping cats...</p>
                        )}
                    </div>
                </div>
            </div>
        </div>
    );
};

const mapStateToProps = (state) => ({
    init: state.init,
});

export default connect(mapStateToProps)(withRouter(ShopByCats));
