import axios from "axios";
import * as Types from "./types";

export const loadPages =
    (oldPages = [], slug = "") =>
    (dispatch) => {
        axios
            .post("/v1/pages", { slug: slug })
            .then((response) => {
                const responseData = response.data;
                const result = responseData.result;
                const newPages = { [slug]: result };
                dispatch({
                    type: Types.LOAD_PAGES,
                    payload: {
                        pages: {
                            status: true,
                            result: { ...oldPages, ...newPages },
                        },
                    },
                });
            })
            .catch((error) => {
                console.log(error);
            });
    };
